@mixin ul-style {
    list-style-type: none;
    padding-left: 0;
    margin: 0;
}

@mixin nav-font {
    font-family: "Montserrat", "Arial", sans-serif;
    font-weight: 400;
    font-size: 16px;
}

@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin title-font {
    font-family: "Dela Gothic One", "Arial", sans-serif;
    line-height: 1.1;
    font-size: 14px;
    text-transform: uppercase;
    text-align: center;
    padding-bottom: 50px;

    @media (min-width: 920px) {
        font-size: 30px;
    }

    @media (min-width: 1200px) {
        font-size: 46px;
    }

    @media (min-width: 1440px) {
        padding-bottom: 80px;
    }
}

