.reviews {
    position: relative;

    &__title {
        @include title-font;
        padding-bottom: 30px;
        font-size: 24px;
    }

    &__wrapper {
        padding-bottom: 80px;
        margin: 0 auto;

        @media (min-width: 1440px) {
            max-width: 1030px;
        }
    }

    &__content {
        overflow: hidden;
    }
}
